body,
html {
  height: 100%;
  margin: 0;
}
body {
  padding: 0;
  height: 100%;
  color: white;
  background-color: #343a40;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h4 {
  font-size: 20px;
  font-weight: 400;
}
#root,
.App {
  height: 100%;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.home {
  width: 100%;
  height: calc(100vh + 20px);
  transition: opacity 1.5s;
  opacity: 1;
  position: relative;
  top: 0;
}
.home.hide {
  opacity: 0;
}
.home-wrapper {
  background: url("https://hello.travefy.com/wp-content/uploads/2015/06/beautiful-beach-resort-ocean-view-infinity-pool.jpg")
    no-repeat center;
  background-size: cover;
  position: relative;
  top: -200px;
}

.blocker,
.youtube-wrapper,
.youtube-wrapper > div {
  width: 100%;
  height: 100%;
}
.youtube-wrapper::after {
  content: "";
  width: 100%;
  height: 100%;
  background: transparent;
  position: absolute;
  top: 0;
  left: 0;
}
.header {
  z-index: 999;
  color: #ce9b51;
  font-weight: 700;
  line-height: 24px;
}
.top-bar {
  background-color: #343a40;
}
.navbar-brand,
.nav-link:hover,
.nav-link {
  color: white;
}
.navbar-brand {
  font-size: 54px;
  font-weight: 600;
}
.nav-link {
  font-size: 14px;
  font-weight: 600;
}
.dropdown:hover .dropdown-menu {
  display: block;
}
.dropdown-menu {
  background-color: #fff;
  border-top: 3px solid #ffb300;
}
.button:hover {
  cursor: pointer;
}
.search-button {
  border: 3px solid #ffb300;
}
.search-button:hover {
  background-color: #ffb300;
}
.view-tour {
  background-color: #ffb300;
  color: rgb(51, 51, 51);
  font-size: 20px;
  line-height: 24px;
  font-weight: 700;
  padding: 15px 45px;
  cursor: pointer;
}
.view-tour:hover {
  background-color: white;
  color: #ffb300;
}
.home-bottom {
  height: 100px;
  width: 100%;
  background-color: rgba(67, 67, 67, 0.7);
  bottom: 0;
  position: absolute;
}
select {
  width: 100%;
  border: none;
  -webkit-appearance: none;
  border-radius: 0;
  height: 40px;
  outline: 0;
  padding: 7px 8px;

  background-image: linear-gradient(45deg, transparent 50%, black 50%),
    linear-gradient(135deg, black 50%, transparent 50%),
    radial-gradient(#fff 100%, transparent 100%);
  background-position: calc(100% - 18px) calc(1em + 2px),
    calc(100% - 13px) calc(1em + 2px), calc(100% - 0.5em) 0.5em;
  background-size: 5px 5px, 5px 5px, 1.5em 1.5em;
  background-repeat: no-repeat;
}
