}

.logo img {
	width: auto;
	margin-top: 15px
}

.logo h1 {
	font-weight: 400;
	margin: 0 0 4px;
	text-transform: capitalize;
	line-height: 44px;
	font-size: 40px;
	font-family: 'Oswald';
	color: #dda668;
	letter-spacing: 4px
}

.logo p {
	font-size: 13px;
	color: #fff;
	display: block;
	line-height: normal;
	letter-spacing: 2px
}

.toggle {
	display: none;
	position: relative;
	right: 0;
	top: 0;
	margin: 18px 0 0;
	text-transform: uppercase;
	font-weight: 700
}

.toggle a {
	width: auto;
	padding: 28px 25px;
	text-align: left;
	border-left: 1px solid #ccc;
	color: #787878
}

.toggle a:before {
	content: '\f0c9';
	color: #fff;
	font-family: FontAwesome;
	position: absolute;
	top: 5px;
	font-size: 28px;
	left: 20px
}

.sitenav {
	position: relative;
	z-index: 9999;
	text-align: center;
	float: right;
	margin-top: 18px
}

.sitenav ul {
	margin: 0;
	padding: 0;
	font-family: 'Roboto Condensed';
	font-size: 14px;
	font-weight: 700;
	text-transform: uppercase;
	letter-spacing: 2px
}

.sitenav ul li {
	display: inline-block;
	position: relative;
	line-height: normal
}

.sitenav ul li a {
	padding: 20px 12px 35px;
	display: block;
	text-align: center;
	color: #fff;
	position: relative
}

.sitenav ul li.current_page_item a,
.sitenav ul li a:hover {
	color: #dda668
}

.sitenav ul li a.parent:before {
	content: '\f107';
	position: absolute;
	right: 0;
	top: 19px;
	font-family: FontAwesome;
	font-size: 18px
}

.sitenav ul li a.parent {
	padding-right: 25px
}

.sitenav ul li ul {
	display: none;
	z-index: 9999;
	position: absolute;
	left: -9999px
}

.sitenav ul li:hover>ul {
	display: block;
	width: 200px;
	position: absolute;
	left: auto;
	top: 72px;
	text-align: left;
	-moz-animation: fadeInUp .3s ease-in-out;
	-webkit-animation: fadeInUp .3s ease-in-out;
	animation: fadeInUp .3s ease-in-out
}

.sitenav ul li ul li {
	display: block;
	position: relative;
	float: none;
	top: 1px;
	line-height: 18px;
	margin-left: 0
}

.sitenav ul li ul li a {
	display: block;
	padding: 10px;
	position: relative;
	top: 0;
	text-align: left
}

.sitenav ul li ul li a.parent:before {
	content: '\f105';
	position: absolute;
	right: 8px;
	top: 9px;
	font-family: FontAwesome;
	font-size: 18px
}

.sitenav ul li:hover ul li:hover>ul {
	display: block;
	position: absolute;
	left: 200px;
	top: -1px;
	-moz-animation: fadeInRight .3s ease-in-out;
	-webkit-animation: fadeInRight .3s ease-in-out;
	animation: fadeInRight .3s ease-in-out
}

