.icon-info-box {
	text-align: center
}

.icon-top {
	margin: 15px auto 30px;
	width: 90px;
	height: 90px;
	border-radius: 50%;
	text-align: center;
	position: relative;
	z-index: 1;
	color: #fff;
	border: 3px solid #dda668;
	-webkit-transition: color 0.3s;
	-moz-transition: color 0.3s;
	transition: color 0.3s;
	font-size: 34px;
	line-height: 84px
}

.icon-top:after {
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	padding: 2px;
	z-index: -1;
	background-color: #dda668;
	position: absolute;
	width: 100%;
	height: 100%;
	border-radius: 50%;
	-webkit-transition: -webkit-transform 0.2s, opacity 0.2s;
	-moz-transition: -moz-transform 0.2s, opacity 0.2s;
	transition: transform 0.2s, opacity 0.2s;
	content: ''
}

.icon-info-box:hover .icon-top::after {
	-webkit-transform: scale(.85);
	-moz-transform: scale(.85);
	-ms-transform: scale(.85);
	transform: scale(.85)
}

.image_info {
